@use "global" as *;

.button{
  display: inline-block;
  font-size: 1.4rem;
  font-weight: $normal;
  color: $white;
  font-family: $second-font-family;
  font-style: normal;
  padding: 1.6rem 8.8rem 1.6rem 4rem ;
  line-height: 2.2rem;
  background-color: $accent-color;
  transition: all 0.3s ease 0s;
  text-align: center;
  position: relative;
  border: 1px solid $accent-color;
  // box-shadow: 0.5rem 0.5rem $accent-color;
}

  .button:hover {
   color: $accent-color;
   background-color: $white;
  }

  .button:before{
    content: "";
    position: absolute;
    top: 50%;
    left: 60%;
    width: 3.4rem;
    height: 0.1rem;
    background:$white;
  }
  .button::after{
   content: '';
   position: absolute;
   top: 42%;
   right:22%;
   width: 1.4rem;
   height:0.1rem;
   color: $accent-color;
   background:$white;
   transform: rotate(35deg);
  }
.button:hover::after {
 background-color: $accent-color;
}
.button:hover::before {
 background-color: $accent-color;
}


